module.exports = "function ToggleButtonExample() {\n  const [checked, setChecked] = useState(false);\n  const [radioValue, setRadioValue] = useState('1');\n\n  const radios = [\n    { name: 'Active', value: '1' },\n    { name: 'Radio', value: '2' },\n    { name: 'Radio', value: '3' },\n  ];\n\n  return (\n    <>\n      <ButtonGroup className=\"mb-2\">\n        <ToggleButton\n          id=\"toggle-check\"\n          type=\"checkbox\"\n          variant=\"secondary\"\n          checked={checked}\n          value=\"1\"\n          onChange={(e) => setChecked(e.currentTarget.checked)}\n        >\n          Checked\n        </ToggleButton>\n      </ButtonGroup>\n      <br />\n      <ButtonGroup className=\"mb-2\">\n        {radios.map((radio, idx) => (\n          <ToggleButton\n            key={idx}\n            id={`radio-${idx}`}\n            type=\"radio\"\n            variant=\"secondary\"\n            name=\"radio\"\n            value={radio.value}\n            checked={radioValue === radio.value}\n            onChange={(e) => setRadioValue(e.currentTarget.value)}\n          >\n            {radio.name}\n          </ToggleButton>\n        ))}\n      </ButtonGroup>\n      <br />\n      <ToggleButton\n        className=\"mb-2\"\n        id=\"toggle-check\"\n        type=\"checkbox\"\n        variant=\"outline-primary\"\n        checked={checked}\n        value=\"1\"\n        onChange={(e) => setChecked(e.currentTarget.checked)}\n      >\n        Checked\n      </ToggleButton>\n      <br />\n      <ButtonGroup>\n        {radios.map((radio, idx) => (\n          <ToggleButton\n            key={idx}\n            id={`radio-${idx}`}\n            type=\"radio\"\n            variant={idx % 2 ? 'outline-success' : 'outline-danger'}\n            name=\"radio\"\n            value={radio.value}\n            checked={radioValue === radio.value}\n            onChange={(e) => setRadioValue(e.currentTarget.value)}\n          >\n            {radio.name}\n          </ToggleButton>\n        ))}\n      </ButtonGroup>\n    </>\n  );\n}\n\nrender(<ToggleButtonExample />);\n"